  position: absolute;
  bottom: 0;
  left: 0;
  content: '';
  height: 2px;
  width: 100%;
  background-color: #fcac45;
  animation: clipMe 0.75s linear;
}

@keyframes clipMe {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

/*  оформление мобильной версии меню и гамбургера */
.mobile_gamburger {
  display: none;
}

.mobile_gamburger {
  width: 50px;
  height: 50px;
  display: none;
  z-index: 100;
}

.toggle-mnu {
  display: block;
  /*   float:right;
position:absolute;
right:10px;top:0; */
  text-align: center;
  line-height: 48px;
}

.toggle-mnu {
  display: block;
  width: 36px;
  height: 36px;
  margin-top: 15px;
  margin-right: 0;
}

.toggle-mnu span:after, .toggle-mnu span:before {
  content: "";
  position: absolute;
  left: 0;
  top: 9px;
}

.toggle-mnu span:after {
  top: 18px;
}

.toggle-mnu span {
  position: relative;
  display: block;
}

.toggle-mnu span, .toggle-mnu span:after, .toggle-mnu span:before {
  width: 100%;
  height: 3px;
  background-color: #fff;
  transition: all .3s;
  backface-visibility: hidden;
  border-radius: 3px;
}

.toggle-mnu.on span {
  background-color: transparent;
}

.toggle-mnu.on span:before {
  -webkit-transform: rotate(45deg) translate(-1px, 0);
  -ms-transform: rotate(45deg) translate(-1px, 0);
  transform: rotate(45deg) translate(-1px, 0);
}

.toggle-mnu.on span:after {
  -webkit-transform: rotate(-45deg) translate(6px, -7px);
  -ms-transform: rotate(-45deg) translate(6px, -7px);
  transform: rotate(-45deg) translate(6px, -7px);
}

.toggle-mnu.on + #menu {
  opacity: 1;
  visibility: visible;
}

/*оформление скрытого меню*/
.hidden-mnu {
